import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import "./style.css";

export const SelectProfile = () => {
  const navigate = useNavigate();
  const [profiles, setProfiles] = useState([]);

  // login_screen  
  const handleGoBack = () => {
    navigate("/login_screen");
  };

  // create_profile
  const handleCreateProfile = () => {
    navigate("/create_profile");
  };

  //   
  useEffect(() => {
    const fetchProfiles = async () => {
      const accessToken = localStorage.getItem("accessToken");
      if (!accessToken) {
        console.error("Access token is not available.");
        return;
      }

      console.log("Access Token:", accessToken); //    

      try {
        const response = await axios.get("http://localhost:8080/api/v1/kids", {
          headers: {
            authorization: `Bearer ${accessToken}`,
          },
        });

        //   ,   
        const { kidsProfiles } = response.data; // kidsProfiles 
        if (Array.isArray(kidsProfiles)) {
          setProfiles(kidsProfiles); // kidsProfiles   
        } else {
          throw new Error("Response data is not an array.");
        }
      } catch (error) {
        console.error(" .", error);
      }
    };

    fetchProfiles();
  }, []);

  //   
  const handleSelectProfile = (id, profileImageUrl, name) => {
    //    localStorage 
    localStorage.setItem("kidId", id);
    localStorage.setItem("kidProfileImageUrl", profileImageUrl);
    localStorage.setItem("kidName", name);

    navigate("/main_page"); //   
  };

  return (
    <div className="select-profile">
      <div className="overlap-wrapper">
        <div className="overlap">
          <div className="overlap-group">
            <img className="polygon" alt="Polygon" src="/img/polygon-1.svg" />
            <div className="ellipse" />
            <div className="div" />

            <div className="profile-autolayer">
              {profiles.map((profile) => (
                <div
                  className="profile-button"
                  key={profile.id}
                  onClick={() =>
                    handleSelectProfile(profile.id, profile.profileImageUrl, profile.name)
                  }
                >
                  <img
                    className="boy-blue"
                    alt={profile.name}
                    src={profile.profileImageUrl}
                  />
                  <div className="text-wrapper">{profile.name}</div>
                </div>
              ))}

              {/*  3   " "   */}
              {profiles.length <= 3 && (

                <div className="create-profile">
                  <img
                    className="img"
                    alt="Create profile"
                    src="/img/createprofilebutton.png"
                    onClick={handleCreateProfile}
                    style={{ cursor: "pointer" }}
                  />
                  <div className="empty-space"></div>
                </div>
              )}
            </div>

            <img
              className="logo-white"
              alt="Logo white"
              src="/img/logowhite.svg"
            />
          </div>
          <img
            className="go-back"
            alt="Go back"
            src="/img/goback-1.svg"
            onClick={handleGoBack}
            style={{ cursor: "pointer" }}
          />
        </div>
      </div>
    </div>
  );
};