import React, { useState } from "react"; // useState 
import { useNavigate } from "react-router-dom"; // useNavigate 
import "./style.css";
import axios from "axios"; // axios 

export const SignUpScreen = () => {
  const [email, setEmail] = useState(""); //   
  const [password, setPassword] = useState(""); //   
  const [name, setName] = useState(""); //   
  const [phoneNumber, setPhoneNumber] = useState(""); //    
  const navigate = useNavigate(); // navigate  

  const handleSignUp = async () => {
    try {
      const response = await fetch("http://localhost:8080/api/v1/auth/signup", {
        method: "POST",
        headers:{
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          email: email,
          password: password,
          name: name,
          phoneNum: phoneNumber
        })
      });

      const data = await response.json();

      if(response.ok){
        alert(" ");
        navigate("/login_screen");
      } else {
        alert(" ");
      } 

    } catch (error) {
      console.error("   ", error);
    }
  };

  const handleGoBack = () => {
    navigate(-1); //   
  };

  return (
    <div className="sign-up-screen">
      <div className="overlap-wrapper">
        <div className="overlap">
          <div className="overlap-group">
            <div className="frame-2">
              <img
                className="img"
                alt="Yellow bear"
                src="/img/yellow-bear-1.png"
              />
              <img
                className="three-animals-2"
                alt="Three animals"
                src="/img/three-animals-1.png"
              />
            </div>

            <div className="sign-up-frame">
              <div className="sign-up-frame-inside">
                <img
                  className="logo-black-2"
                  alt="Logo black"
                  src="/img/logoblack-1.png"
                />

                <div className="sign-up-component">
                  <div className="sign-up-component-2">
                    <input
                      className="email-field"
                      placeholder=""
                      value={email} //   
                      onChange={(e) => setEmail(e.target.value)} //  
                    />

                    <input
                      className="text-input-wrapper"
                      placeholder=""
                      type="password" //   
                      value={password} //   
                      onChange={(e) => setPassword(e.target.value)} //  
                    />

                    <input
                      className="text-input-wrapper"
                      placeholder=""
                      value={name} //   
                      onChange={(e) => setName(e.target.value)} //  
                    />

                    <input
                      className="text-input-wrapper"
                      placeholder=""
                      value={phoneNumber} //    
                      onChange={(e) => setPhoneNumber(e.target.value)} //  
                    />
                  </div>

                  <div className="sign-up-button" onClick={handleSignUp}>
                    <div className="text-wrapper-2"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <img
            className="go-back"
            alt="Go back"
            src="/img/goback.png"
            onClick={handleGoBack} //    
            style={{ cursor: 'pointer' }} //    
          />
        </div>
      </div>
    </div>
  );
};