import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { Frame28 } from "../../icons/Frame28";
import { Frame29 } from "../../icons/Frame29";
import "./style.css";

export const BookDetail = () => {
  const navigate = useNavigate();
  const [kidProfileImageUrl, setKidProfileUrl] = useState("");
  const [kidId, setKidId] = useState("");
  const [bookId, setBookId] = useState("");
  const [title, setTitle] = useState("");
  const [story, setStory] = useState("");
  const [author, setAuthor] = useState("");

  // kidProfileImageUrl localStorage 
  useEffect(() => {
    const profileImage = localStorage.getItem("kidProfileImageUrl");
    if (profileImage) {
      setKidProfileUrl(profileImage);
    }
  }, []);

  // kidId localStorage 
  useEffect(() => {
    const kidId = localStorage.getItem("kidId");
    if (kidId) {
      setKidId(kidId);
    }
  }, []);

  // bookId localStorage 
  useEffect(() => {
    const bookId = localStorage.getItem("bookId");
    if (bookId) {
      setBookId(bookId);
    }
  }, []);

  //    
  useEffect(() => {
    const fetchBookDetail = async () => {
      try {
        const response = await fetch(`http://localhost:8080/api/v1/books?bookId=${bookId}&kidId=${kidId}`);
        if (!response.ok) throw new Error("    .");

        const data = await response.json();

        setTitle(data.title);
        setStory(data.story);
        setAuthor(data.author);
        //      
      } catch (error) {
        console.error("     :", error);
      }
    };

    if (bookId && kidId) {
      fetchBookDetail();
    }
  }, [bookId, kidId]);

  //      
  const handleLikeBook = async () => {
    try {
      const reqBody = { kidId: Number(kidId) }; //   
      const response = await fetch(`http://localhost:8080/api/v1/books/${bookId}/like`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(reqBody),
      });

      if (!response.ok) throw new Error("   .");
      console.log(" .");
    } catch (error) {
      console.error("     :", error);
    }
  };

  //      
  const handleDislikeBook = async () => {
    try {
      const reqBody = { kidId: Number(kidId) }; //   
      const response = await fetch(`http://localhost:8080/api/v1/books/${bookId}/dislike`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(reqBody),
      });

      if (!response.ok) throw new Error("   .");
      console.log(" .");
    } catch (error) {
      console.error("     :", error);
    }
  };

  const handleDrawButtonClick = () => {
    navigate("/draw");
  };

  return (
    <div className="book-detail">
      <div className="div-2">
        <div className="overlap-group-2">
          <div className="rectangle-2" />
          <div className="frame-8">
            <div className="frame-9">
              <img className="img" alt="" src="/img/logo-white-1.svg" />
              <div className="frame-10">
                <img className="yellow-bear-2" alt=" " src="/img/yellow-bear.png" />
                <img className="three-animals-2" alt="  " src="/img/three-animals.png" />
              </div>
            </div>

            <div className="frame-22">
              <div className="frame-23">
                <div className="frame-24">
                  <div className="text-wrapper-10"></div>
                </div>
                <div className="frame-24">
                  <div className="text-wrapper-10"></div>
                </div>
                <div className="frame-24">
                  <button className="text-wrapper-10" onClick={handleDrawButtonClick}>
                     
                  </button>
                </div>
              </div>
              <div className="profile-picture-container">
                <img className="kid-profile" alt=" " src={kidProfileImageUrl} style={{ width: "100px", height: "100px" }} />
              </div>
            </div>
          </div>
        </div>

        <div className="frame-14">
          <div className="frame-77">
            <img className="yellow-box" alt="" src="/img/rectangle-2.svg" />
            <div className="frame-88">
              <button className="icon-button" onClick={handleLikeBook} style={{ background: 'none', border: 'none', padding: 0 }}>
                <Frame28 className="icon-instance-node" /> {/*     */}
              </button>
              <button className="icon-button" onClick={handleDislikeBook} style={{ background: 'none', border: 'none', padding: 0 }}>
                <Frame29 className="icon-instance-node" /> {/*     */}
              </button>
            </div>
          </div>

          <div className="frame-16">
            <div className="frame-17">
              <div className="frame-18">
                <div className="text-wrapper-7"></div>
                <div className="rectangle-3" />
                <div className="text-wrapper-8">{title}</div>
              </div>

              <div className="frame-18">
                <div className="text-wrapper-9"></div>
                <div className="rectangle-3" />
                <div className="text-wrapper-8">{author}</div>
              </div>

              <div className="frame-18">
                <div className="text-wrapper-9"></div>
                <div className="rectangle-3" />
                <div className="text-wrapper-8">{story}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};